import React, { useEffect, useState } from 'react'
import $ from "jquery";
import { set } from 'js-cookie';
export default function AddCoursePopUp() {
    const [name, setName] = useState('')
    const [time, setTime] = useState('')
    const [price, setPrice] = useState('')
    const [subject, setSubject] = useState('')
    const [lecturer, setLecturer] = useState('')
    const [chapters, setChapters] = useState([])
    const addChapter = () =>{
        setChapters([...chapters].push({name:'',lessons:[]}))
    }
    useEffect(() => {
        const inFile = document.getElementById("fileInput");
        const imgSelect = document.querySelector(".ava-selected");
        const imgSelectAvatar = document.getElementById("select-avatar");
        inFile.addEventListener("change", function () {
            const file = this.files[0];

            if (file) {
                const reader = new FileReader();
                imgSelect.style.display = "block";
                imgSelectAvatar.style.display = "none";
                reader.addEventListener("load", function () {
                    imgSelect.setAttribute("src", this.result);
                });
                reader.readAsDataURL(file);
            }
        });
        return () => {};
    }, []);
    useEffect(() => {
        function ActivePopup(){
            $(".popup-detail").css('transform', 'scale(1)');
            $(".main").addClass("blur");
        };
        $(".popup-inclick").click(function(e){
            if ($(".popup-container-detail").hasClass("popup-is-active")) {
                $(".popup-container-detail").removeClass('popup-is-active')
            }
            ActivePopup();
        });
        $(".popup-detail").on("click",function(){
            $(".popup-detail").css('transform', 'scale(0)');
            $(".main").removeClass("blur");
        });
        $(".popup-container-detail").on('click', function(e) {
            e.stopPropagation();
            /* Act on the event */
        });
        function ActivePopupAdd(){
            $(".popup-add").css('transform', 'scale(1)');
            $(".main").addClass("blur");
        };
        $(".addnew").click(function(e){
            if ($(".popup-container-add").hasClass("popup-is-active")) {
                $(".popup-container-add").removeClass('popup-is-active')
            }
            ActivePopupAdd();
        });
        $(".popup-add").on("click",function(){
            $(".popup-add").css('transform', 'scale(0)');
            $(".main").removeClass("blur");
        });
        $(".popup-container-add").on('click', function(e) {
            e.stopPropagation();
            /* Act on the event */
        });
        return () => {
            
        }
    }, [])
    return (
        <div class="popup popup-add" id="add">
                <div class="popup-container-add" id="popup-container-add">
                    <div class="container-fluid">
                        <div class="row">
                            <div class="col-lg-4 col-md-4 col-sm-4 col-xs-12">
                                <div class="row">
                                    <div class="avatar w325 h325">
                                        <div class="avatar-img" style={{ alignItems: "center" }}>
                                            <img
                                                id="select-avatar"
                                                src="/Images/AdminPage/add-insert-control-point-plus-31700.png"
                                                alt=""
                                            />
                                            <input id="fileInput" type="file" />
                                            <img
                                                class="ava-selected "
                                                id="avatar-selected"
                                                src=""
                                                alt=""
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="col-lg-8 col-md-8 col-sm-8 col-xs-12">
                                <div class="input-profile-detail">
                                    <div class="input-item">
                                        <div class="row">
                                            <div class="col-lg-6 col-md-6 col-sm-6 col-xs-12">
                                                <input type="text" value={name} onChange={(e)=>{setName(e.target.value)}} placeholder="Tên Khoá Học" />
                                            </div>
                                            <div class="col-lg-6 col-md-6 col-sm-6 col-xs-12">
                                                <input type="text" value={time} onChange={(e)=>{setTime(e.target.value)}} placeholder="Thời Gian Học" />
                                            </div>
                                        </div>
                                    </div>
                                    <div class="input-item">
                                        <div class="row">
                                            <div class="col-lg-6 col-md-6 col-sm-6 col-xs-12">
                                                <input type="text" value={price} onChange={(e)=>{setPrice(e.target.value)}} placeholder="Giá Tiền" />
                                            </div>
                                            <div class="col-lg-6 col-md-6 col-sm-6 col-xs-12">
                                                <input type="text" value={lecturer} onChange={(e)=>{setLecturer(e.target.value)}} placeholder="Giảng Viên" />
                                            </div>
                                        </div>
                                    </div>
                                    <div class="input-item">
                                        <div class="row">
                                            <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                                                <select name="" id="slUniversity" onChange={(e)=>{setSubject(e.target.value)}} value={subject}>
                                                    <option value="">Thể Loại</option>
                                                    <option value="Công Nghệ Thông Tin">Công Nghệ Thông Tin</option>
                                                    <option value="Âm Nhạc">Âm Nhạc</option>
                                                    <option value="Kinh Tế">Kinh Tế</option>
                                                    <option value="Kỹ Năng Mềm">Kỹ Năng Mềm</option>
                                                    <option value="Nhiếp ảnh">Nhiếp ảnh</option>
                                                    <option value="Thiết kế đồ họa">Thiết kế đồ họa</option>
                                                </select>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="input-item">
                                        <div class="row">
                                            <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                                                <textarea
                                                    placeholder="Giới thiệu về khoá học"
                                                    name=""
                                                    id=""
                                                    cols="69"
                                                    rows="7"
                                                ></textarea>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="input-item">
                                        <div class="row">
                                            <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                                                <button id="add-chapter" type="button" class="btn btn-primary" onClick={addChapter}>
                                                    Thêm Bài
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="input-item">
                                        <div class="row">
                                            <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                                                <div class="all-chapter-in-course" id="id-all-chapter"></div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="submit text-right">
                                        <div class="row">
                                            <div class="col-lg-10 col-md-10 col-sm-10 col-xs-12">
                                                <button type="button" class="btn btn-primary">
                                                    Thêm Khoá Học
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
    )
}
